export default {
  // 
  // 
  gradeName($arr) {
    const d = []
    for (let p of $arr) {
      switch (p) {
        case 1:
          d.push({ id: 1, name: '' })
          break
        case 2:
          d.push({ id: 2, name: '' })
          break
        case 3:
          d.push({ id: 3, name: '' })
          break
        case 4:
          d.push({ id: 4, name: '' })
          break
        case 5:
          d.push({ id: 5, name: '' })
          break
        case 6:
          d.push({ id: 6, name: '' })
          break
        case 7:
          d.push({ id: 7, name: '' })
          break
        case 8:
          d.push({ id: 8, name: '' })
          break
        case 9:
          d.push({ id: 9, name: '' })
          break
        default:
          return
      }
    }
    return d
  },
  // 
  getGradeName(num) {
    let d = ''
    switch (num) {
      case 1:
        d = ''
        break
      case 2:
        d = ''
        break
      case 3:
        d = ''
        break
      case 4:
        d = ''
        break
      case 5:
        d = ''
        break
      case 6:
        d = ''
        break
      case 7:
        d = ''
        break
      case 8:
        d = ''
        break
      case 9:
        d = ''
        break
      default:
        return
    }
    return d
  },
  // id
  getGradeNum(name) {
    let d = 0
    switch (name) {
      case '':
        d = 1
        break
      case '':
        d = 2
        break
      case '':
        d = 3
        break
      case '':
        d = 4
        break
      case '':
        d = 5
        break
      case '':
        d = 6
        break
      case '':
        d = 7
        break
      case '':
        d = 8
        break
      case '':
        d = 9
        break
      default:
        return
    }
    return d
  },

  ///////////////////////////////////////////////////////////

  // 
  excludeRepeat(oldArr, repeatKey) {
    const newArr = []
    for (let i = 0; i < oldArr.length; i++) {
      let flag = true
      for (let j = 0; j < newArr.length; j++) {
        if (oldArr[i][repeatKey] === newArr[j]) {
          flag = false
        }
      }
      if (flag) {
        newArr.push(oldArr[i][repeatKey])
      }
    }
    return newArr
  },
  // 
  compare($obj, $key) {
    let compareObj = {}
    for (let i = 0, len = $obj.length; i < len; i++) {
      if ($obj[i][$key] !== '') {
        if (compareObj[$obj[i][$key]] === undefined) {
          compareObj[$obj[i][$key]] = 1
        } else {
          compareObj[$obj[i][$key]] = compareObj[$obj[i][$key]] + 1
        }
      }
    }
    return compareObj
  },
  // 
  compareSort($arr, key, Asc) {
    // 
    function compareFunc(propertyName) {
      if (Asc) {
        return function (obj1, obj2) {
          var val1, val2
          val1 = obj1[propertyName]
          val2 = obj2[propertyName]
          if (val1 < val2) {
            return -1
          } else if (val1 > val2) {
            return 1
          } else {
            return 0
          }
        }
      } else {
        return function (obj1, obj2) {
          var val1, val2
          val1 = obj1[propertyName]
          val2 = obj2[propertyName]
          if (val1 < val2) {
            return 1
          } else if (val1 > val2) {
            return -1
          } else {
            return 0
          }
        }
      }
    }
    const copy = $arr.slice(0)
    return copy.sort(compareFunc(key))
  },
  // 
  initialSort($arr, keyName) {
    return $arr.sort((a, b) => a[keyName].localeCompare(b[keyName], 'zh-Hans-CN', { sensitivity: 'accent' }))
  }
}
