import { changeGardenInfo, changeDetailsClassName, changeClassList } from '../actions/kindergarten-actions';

const initialState = {
  gardenInfo: {
    gardenID: 1405,
    isParentGarden: true
  },
  classList: [{ classId: 9893, className: "" }, { classId: 9894, className: "" }, { classId: 9895, className: "" }, { classId: 34658, className: "" }, { classId: 44988, className: "" }, { classId: 75172, className: "" }, { classId: 75173, className: "" }, { classId: 75175, className: "" }, { classId: 75192, className: "" }, { classId: 75193, className: "" }, { classId: 75194, className: "" }, { classId: 75196, className: "" }, { classId: 75197, className: "" }, { classId: 75198, className: "" }, { classId: 76716, className: "" }, { classId: 77266, className: "" }, { classId: 77282, className: "" }, { classId: 77472, className: "" }, { classId: 77473, className: "2" }, { classId: 77474, className: "" }, { classId: 77475, className: "" }, { classId: 77476, className: "5" }, { classId: 77477, className: "6" }
  ],
  detailsClassName: ''
}

export default function (state = initialState, action) {
  switch (action.type) {
    case changeGardenInfo: {
      return {
        ...state,
        gardenInfo: action.payload
      }
    }
    case changeClassList: {
      return {
        ...state,
        classList: action.payload
      }
    }
    case changeDetailsClassName: {
      return {
        ...state,
        detailsClassName: action.payload
      }
    }
    default:
      return state;
  }
}