import React, {Component} from 'react';
import { NavBar, Icon, WhiteSpace, Grid, Toast } from 'antd-mobile';
import './style.less';

class App extends Component {

  constructor(props) {
    super(props);
    this.state = {
      isLogin: false
    }
  }

  componentDidMount() {
    if (this.props.location.query && this.props.location.query.fromLogin) {
      Toast.info('', 1)
    }
  }

  navClick = (e) => {
    this.props.history.push(e.url)
  }

  render() {
    const layout = {
      braceletState: [{
        url: '/president/braceletActivate',
        icon: require('../../.././assets/images/bracelet_l.png'),
        text: ''
      }, {
        url: '/president/braceletElectric',
        icon: require('../../.././assets/images/bracelet_r.png'),
        text: ''
      }],
      dataExpression: [{
        url: '/president/gymnastics',
        icon: require('../../.././assets/images/huwai.png'),
        text: ''
      }, {
        url: '/president/sleep',
        icon: require('../../.././assets/images/kexue.png'),
        text: ''
      }, {
        url: '/president/sport',
        icon: require('../../.././assets/images/huiben.png'),
        text: ''
      }, {
        url: '/president/areaAngle',
        icon: require('../../.././assets/images/shougong.png'),
        text: ''
      }]
    }
    return (
      <div className="home">
          <NavBar
            mode="light"
            icon={<Icon type="left" />}
            onLeftClick={() => {this.props.history.push('/login')}}
          ></NavBar>
          <WhiteSpace size="md" />
          <div>
            <div className="c-title"></div>
            <Grid data={layout.braceletState} columnNum={2} hasLine={false} activeStyle={false} onClick={this.navClick} />
          </div>
          <WhiteSpace size="md" />
          <div>
            <div className="c-title"></div>
            <Grid data={layout.dataExpression} columnNum={2} hasLine={false} activeStyle={false} onClick={this.navClick} />
          </div>
      </div>
    );
  }
}

export default App;